body {
  background-color: #9266ff;
}

.App {
  background-color: #3f3160;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
}

#form-todo input{
  background-color: rgb(27,112,137);
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0px 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 10px;
  color: white;
}

#form-todo input::placeholder {
  color: rgba(177, 14, 14, 0.5)
}

#form-todo input:focus,#form-todo button:focus{
  outline: none;
}

#form-todo button{
  height: 40px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  background-color: rgb(202,156,24);
  cursor:pointer;
  font-weight: bolder;
  color:white;
}