.list{
    color:white;
    width: 300px;
    height: 30px;
    margin: 20px auto;
    background-color: rgb(221, 19, 221);
    border-radius: 5px;

}

.list p{
 padding: 10px auto;
 position: relative;
}

.list p span{
    position: absolute;
    right: 10px;
    cursor: pointer;
}

.list p input{
    background-color: transparent;
    border: 0;
    color: white;
    font-size: 18px;
    
}
.list p input:focus{
    outline: none;
}